
#recommend {
  height: 100%;
  font-size: 0;
  background-color: #F4F4F4;
}
#recommend .wrapper {
    width: 100%;
    overflow: hidden;
    position: relative;
}
#recommend .wrapper .slide-wrapper {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      width: 700%;
}
#recommend .wrapper .slide-wrapper .slide-item {
        width: 100%;
}
#recommend .wrapper .slide-wrapper.transition {
        transition: -webkit-transform .5s ease-in-out;
        transition: transform .5s ease-in-out;
        transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
}
#recommend .wrapper .points-wrapper {
      position: absolute;
      width: 100%;
      bottom: 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
#recommend .wrapper .points-wrapper .point-item {
        width: 6px;
        height: 6px;
        margin: 0 6px 0 6px;
        background-color: gray;
        border-radius: 50%;
}
#recommend .wrapper .points-wrapper .point-item.active {
          background-color: #fff;
}
#recommend .content {
    width: 100%;
    font-size: 0;
    padding-top: 8px;
}
#recommend .content .title {
      font-size: 14px;
      color: #666;
      padding-left: 8px;
      line-height: 14px;
}
#recommend .content .radio_cards {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding: 8px;
}
#recommend .content .radio_cards .card {
        background-color: #fff;
        padding-bottom: 16px;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
}
#recommend .content .radio_cards .card .img_wrapper {
          position: relative;
          margin-bottom: 5px;
}
#recommend .content .radio_cards .card .img_wrapper span.icon {
            position: absolute;
            right: 8px;
            bottom: 8px;
}
#recommend .content .radio_cards .card .img_wrapper .icon {
            display: inline-block;
            width: 25px;
            height: 25px;
}
#recommend .content .radio_cards .card .img_wrapper .icon_play {
            background: url("https://y.gtimg.cn/mediastyle/mobile/yqq_v5/img/list_sprite.png?max_age=19830212&d=20151105145423") 0 0 no-repeat;
            background-size: cover;
}
#recommend .content .radio_cards .card .title {
          color: #555;
}
#recommend .content .radio_cards .card:first-child {
          margin-right: 8px;
}
#recommend .content .message {
      font-size: 14px;
      padding-top: 30px;
      text-align: center;
}
#recommend .content .message .go {
        display: block;
        color: #666;
        margin-top: 16px;
}
#recommend .footer {
    margin-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}
#recommend .footer .footer-logo {
      display: inline-block;
      width: 82px;
      height: 24px;
      background: url("https://y.gtimg.cn/mediastyle/mobile/yqq_v5/img/logo_footer.png?max_age=19830212&d=20151105145423") 0 0 no-repeat;
      background-size: cover;
}



