
#songlistshow {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #694C39;
}
#songlistshow .wrapper .cover {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding-top: 100%;
    background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.7), transparent), url("https://y.gtimg.cn/music/photo_new/T002R300x300M000000noD0i1tPu1N.jpg?max_age=2592000") 0 0 no-repeat;
    background-size: cover;
}
#songlistshow .wrapper .cover .close {
      position: absolute;
      left: 10px;
      top: 10px;
      height: 36px;
      color: #666;
      font-size: 24px;
      line-height: 36px;
      text-align: center;
      font-weight: 500;
}
#songlistshow .wrapper .cover .close:hover {
        background-color: rgba(255, 255, 255, 0.8);
}
#songlistshow .wrapper .cover .info {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background: -webkit-linear-gradient(bottom, black, transparent);
      padding: 6px 0 20px 6px;
      font-size: 0;
}
#songlistshow .wrapper .cover .info span {
        display: block;
        margin: 5px;
}
#songlistshow .wrapper .cover .info .title {
        font-size: 22px;
        color: #fff;
        margin-bottom: 8px;
}
#songlistshow .wrapper .cover .info .time {
        font-size: 12px;
        color: #fff;
}
#songlistshow .wrapper .cover .info .play {
        width: 48px;
        height: 48px;
        border: 3px solid #fff;
        border-radius: 50%;
        position: absolute;
        right: 10px;
        bottom: 10px;
}
#songlistshow .wrapper .cover .info .play::before {
          content: '';
          position: absolute;
          left: 18px;
          top: 14px;
          width: 0;
          height: 0;
          border-width: 10px 16px;
          border-style: solid;
          border-color: transparent transparent transparent #fff;
}
#songlistshow .wrapper .songlist {
    width: 100%;
    font-size: 0;
    padding-top: 100%;
    list-style: none;
}
#songlistshow .wrapper .songlist .song-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 60px;
}
#songlistshow .wrapper .songlist .song-item .index {
        font-size: 20px;
        -webkit-box-flex: 0;
            -ms-flex: 0 0 60px;
                flex: 0 0 60px;
        height: 100%;
        color: #eee;
        text-align: center;
        line-height: 60px;
}
#songlistshow .wrapper .songlist .song-item .song-info {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        position: relative;
        padding-left: 6px;
}
#songlistshow .wrapper .songlist .song-item .song-info .songname {
          display: block;
          font-size: 16px;
          width: 100%;
          box-sizing: border-box;
          color: #fff;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          padding-top: 10px;
          padding-bottom: 4px;
}
#songlistshow .wrapper .songlist .song-item .song-info .singername {
          font-size: 14px;
          color: #aaa;
}
#songlistshow .wrapper .songlist .song-item .song-info::after {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          content: '';
          display: block;
          border-bottom: 1px solid #ccc;
          -webkit-transform: scaleY(0.2);
                  transform: scaleY(0.2);
}



