
#search .search-box {
  padding: 10px 10px;
  background-color: #f4f4f4;
  text-align: center;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#search .search-box #input_search {
    border-radius: 5px;
    box-sizing: border-box;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    height: 36px;
    padding-left: 36px;
    font-size: 14px;
    color: #666;
}
#search .search-box #input_search:focus {
      outline: none;
}
#search .search-box .cancel {
    display: inline-block;
    width: 60px;
    height: 100%;
    line-height: 36px;
    font-size: 14px;
    text-align: center;
    color: #666;
}
#search .search-box::before {
    content: '';
    width: 18px;
    height: 18px;
    position: absolute;
    top: 19px;
    left: 19px;
    background: url("data:image/png;base64,");
    background-size: cover;
}
#search .tips {
  padding: 16px 0 0 10px;
}
#search .tips .title {
    font-size: 14px;
    font-weight: 600;
    color: #666;
}
#search .tips .tiplist {
    padding-top: 10px;
    text-align: center;
}
#search .tips .tiplist .tip-item {
      display: inline-block;
      padding: 5px 8px;
      border: 1px solid #666;
      font-size: 14px;
      margin: 4px;
      border-radius: 12px;
      color: #666;
      cursor: pointer;
}
#search .result-content {
  font-size: 0;
}
#search .result-content .result-list .result-item {
    padding: 6px 0 6px 6px;
    width: 100%;
    box-sizing: border-box;
    border-bottom: 1px solid #e0e0e0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
#search .result-content .result-list .result-item .avatar {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
}
#search .result-content .result-list .result-item .avatar .img_avatar {
        width: 100%;
}
#search .result-content .result-list .result-item .info {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      overflow: hidden;
      height: 50px;
      box-sizing: border-box;
      padding: 6px 0 6px 14px;
}
#search .result-content .result-list .result-item .info span {
        width: 80%;
        box-sizing: border-box;
        display: inline-block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
#search .result-content .result-list .result-item .info .songname {
        font-size: 16px;
        color: #666;
}
#search .result-content .result-list .result-item .info .singername {
        font-size: 12px;
        color: #666;
        margin-top: 6px;
}



