
#songpage {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  font-size: 0;
  background-color: #fff;
}
#songpage .wrapper {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
}
#songpage .wrapper .header {
      padding: 16px;
      width: 100%;
      box-sizing: border-box;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      background-color: rgba(0, 0, 0, 0.1);
      position: relative;
}
#songpage .wrapper .header .avatar {
        width: 80px;
}
#songpage .wrapper .header .avatar .img_avatar {
          width: 100%;
}
#songpage .wrapper .header .play-status {
        position: absolute;
        bottom: 30px;
        right: 20px;
        width: 45px;
        height: 45px;
        border: 1px solid #fff;
        border-radius: 50%;
        opacity: .6;
}
#songpage .wrapper .header .play-status::after {
          content: '';
          display: inline-block;
          margin: 12px 17px;
          width: 17px;
          height: 20px;
          background-size: 40px 350px;
          background-image: url(https://y.gtimg.cn/mediastyle/mobile/app/share/img/sprite_play.png?max_age=2592000&d=20151203183007);
          background-repeat: no-repeat;
}
#songpage .wrapper .header .pause::after {
        background-position: 0 -150px;
}
#songpage .wrapper .header .play::after {
        background-position: 0 -180px;
        margin: 15px 19px;
}
#songpage .wrapper .header .info {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        padding: 12px 0 0 10px;
}
#songpage .wrapper .header .info span {
          display: block;
          color: #fff;
}
#songpage .wrapper .header .info .name {
          width: 100%;
          font-size: 18px;
}
#songpage .wrapper .header .info .singername {
          font-size: 14px;
          margin-top: 10px;
}
#songpage .wrapper .lyric {
      font-size: 16px;
      color: #888;
      text-align: center;
      margin-top: 40px;
      position: absolute;
      top: 112px;
      left: 0;
      right: 0;
      bottom: 130px;
      overflow: hidden;
}
#songpage .wrapper .lyric .lyricList {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        transition: all .4s linear;
}
#songpage .wrapper .lyric .lyricList .lyric-item {
          line-height: 46px;
          font-weight: 500;
}
#songpage .wrapper .lyric .lyricList .lyric-item.active {
            color: #31C27C;
}
#songpage .wrapper .control {
      position: absolute;
      bottom: 0;
      height: 146px;
      left: 0;
      right: 0;
}
#songpage .wrapper .control .options {
        margin-top: 30px;
        padding-right: 30px;
        text-align: right;
}
#songpage .wrapper .control .options .back {
          float: left;
          width: 40px;
          height: 30px;
          padding-left: 16px;
          font-size: 20px;
          color: #888;
          font-weight: 400;
}
#songpage .wrapper .control .options .favorite {
          display: inline-block;
          width: 26px;
          height: 21px;
          background-image: url(https://y.gtimg.cn/mediastyle/mobile/app/share/img/sprite_play.png?max_age=2592000&d=20151203183007);
          background-size: 40px 350px;
          background-repeat: no-repeat;
}
#songpage .wrapper .control .process {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 46px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
#songpage .wrapper .control .process .time_start,
        #songpage .wrapper .control .process .time_end {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 16px;
                  flex: 0 0 16px;
          line-height: 16px;
          color: rgba(255, 255, 255, 0.2);
          font-size: 12px;
          padding: 0 14px;
          font-weight: 200;
}
#songpage .wrapper .control .process .bg_process {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          height: 2px;
          margin: auto;
          background-color: rgba(255, 255, 255, 0.2);
          border-radius: 2px;
}
#songpage .wrapper .control .process .bg_process .mask_process {
            width: 0%;
            height: 2px;
            background-color: #31C27C;
}
#songpage .bg {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 1;
    background-size: cover;
    background-position: bottom center;
    background-color: #fff;
    -webkit-filter: blur(15px);
            filter: blur(15px);
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}
#songpage .bg_mask {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 2;
    background-color: #000000;
    opacity: .6;
}



