
#ranking {
  font-size: 0;
}
#ranking .ranking-wrapper {
    background-color: #f4f4f4;
    padding: 0 10px;
}
#ranking .ranking-wrapper .ranking-item {
      padding: 10px 0 0 0;
}
#ranking .ranking-wrapper .ranking-item .wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        background-color: #fff;
        height: 100px;
}
#ranking .ranking-wrapper .ranking-item .wrapper .avatar {
          width: 100px;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content {
          position: relative;
          padding: 6px 4px 4px 10px;
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          font-size: 14px;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content .title {
            font-size: 16px;
            font-weight: 400;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content .threesong {
            padding-top: 10px;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content .threesong .song-item {
              font-size: 14px;
              font-weight: 200;
              margin-bottom: 3px;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content .threesong .song-item .singername {
                font-size: 12px;
                color: #666;
                display: inline-block;
                width: 60px;
                padding: 1px 0;
                vertical-align: middle;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
}
#ranking .ranking-wrapper .ranking-item .wrapper .content::after {
            content: '>';
            width: 6px;
            height: 6px;
            color: #666;
            position: absolute;
            right: 14px;
            bottom: 50%;
            margin-top: -3px;
}



