
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app .mob_header {
    width: 100%;
    height: 44px;
    background-color: #31C27C;
}
#app .mob_header .music_logo {
      float: left;
      width: 90px;
      height: 25px;
      margin: 10px 0 0 8px;
      background: url("https://y.gtimg.cn/mediastyle/mobile/yqq_v5/img/logo.png?max_age=19830212&d=20151105145423") 0 0 no-repeat;
      background-size: cover;
}
#app .mob_header .btn_download {
      float: right;
      font-size: 14px;
      color: #ffffff;
      border-radius: 16px;
      padding: 8px;
      margin: 6px 8px;
      background-color: #149C5A;
}
#app .mob_nav {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
#app .mob_nav .nav-item {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      background-color: #fff;
      text-align: center;
      line-height: 40px;
      font-size: 16px;
      color: #666;
}
#app .mob_nav .nav-item.router-link-active {
      color: #31C27C;
      border-bottom: 2px solid #31C27C;
}



